import Square from "./Square";

const style = {
  display: "grid",
  gap: "10px",
  gridTemplateColumns: "repeat(3, 1fr)"
};

function Grid({ grid }) {
  return (
    <div style={style}>
      <Square title={grid[0][0]} onClick={() => 1} />
      <Square title={grid[0][1]} onClick={() => 1} />
      <Square title={grid[0][2]} onClick={() => 1} />
      <Square title={grid[1][0]} onClick={() => 1} />
      <Square title={grid[1][1]} onClick={() => 1} />
      <Square title={grid[1][2]} onClick={() => 1} />
      <Square title={grid[2][0]} onClick={() => 1} />
      <Square title={grid[2][1]} onClick={() => 1} />
      <Square title={grid[2][2]} onClick={() => 1} />
    </div>
  );
}

export default Grid;
